  import { cateGoods } from 'api/api'
  import { mapMutations, mapGetters } from 'vuex'
  export default {
    data() {
      return {
        menuLists: []
      }
    },
    computed: {
    ...mapGetters([
      'catedes'
    ])
  },
  created() {
    this._getCateDetail()
  },
  methods: {
    // let 
    
  //  cateGoods().then((res)=>{
  //    cosnole.log(res)
  //  })
    back() {
      this.$router.back()
    },
    toDeail(id) {
      let objData = {'id':id}
      this.setGoods(objData)
      this.$router.push({
				path: `/detail?id=${id}`
			})
    },
    _getCateDetail() {
      // console.log(this.catedes.id)
      // 请求数据
      console.log(this.catedes)
      let cid = this.catedes.id
      // let order = 
      cateGoods(cid).then((res)=>{

        // console.log(res)
        if(res.status === 1) {
          this.menuLists = res.data[0].items
        }else{
          console.log('数据出错了')
        }
      })
    },
    ...mapMutations({
      setGoods: 'SET_GOODS'
	  })
  }

  }



